'use client';
import { useState, useEffect } from 'react';
import LoadingSkeleton from './LoadingSkeleton';

export default function ActorList({ actors, loading, onSelect, title }) {
  const [search, setSearch] = useState('');

  const filtered = actors.filter(a =>
    a.name.toLowerCase().includes(search.toLowerCase())
  );

  if (loading) return <LoadingSkeleton />;

  return (
    <div>
      <h3 className="text-lg font-semibold mb-2" style={{ color: '#58a6ff' }}>{title}</h3>
      <input
        type="search"
        placeholder="Search actors..."
        value={search}
        onChange={e => setSearch(e.target.value)}
        className="w-full mb-2 px-3 py-1 bg-[#161b22] text-[#c9d1d9] rounded border border-gray-600 focus:ring-[#58a6ff]"
      />
      <ul className="space-y-2 overflow-auto max-h-64">
        {filtered.length === 0 && <li className="text-gray-500 italic">No actors</li>}
        {filtered.map(a => (
          <li
            key={a.id}
            onClick={() => onSelect(a.id)}
            className="cursor-pointer bg-[#161b22] border border-gray-600 p-2 rounded hover:bg-[#238636] transition"
          >
            <p className="font-medium text-[#c9d1d9]">{a.name}</p>
            <p className="text-sm text-gray-400">{a.title}</p>
          </li>
        ))}
      </ul>
    </div>
  );
}
